//
// breadcrumb.scss
//

.breadcrumbs {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.breadcrumb-item {
  font-size: 22px;
  line-height: $topbar-height;
  font-weight: 500;

  @media (max-width: 767.98px) {
    line-height: $topbar-height-mobile;
  }

  &.active,
  a {
    color: #00f;
  }

  &:before,
  & + .breadcrumb-item:before {
    content: '/';
    font-family: inherit;
    display: inline-block;
    padding-right: 12px;
    color: #00eba8;
    font-weight: 700;
  }

  &:first-child {
    padding-left: 0;
  }
}
