//
// modal.scss
//

// Modal full width
.modal-full-width {
  width: 95%;
  max-width: none;
}

// Modal Positions
.modal-top {
  margin: 0 auto;
}

// Right Modal
.modal-right {
  position: absolute;
  right: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  height: 100%;
  margin: 0;
  background-color: $modal-content-bg;
  align-content: center;
  transform: translate(25%, 0) !important;

  button.close {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 1;
  }
}

.modal {
  &.show {
    .modal-right,
    .modal-dialog {
      transform: translate(0, 0) !important;
    }
  }
}

.close {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  background: #b6ffe8;
  opacity: 1;
  transition: 0.25s;
  color: #000;

  @media (max-width: 767.98px) {
    width: 32px;
    height: 32px;
  }

  &:hover {
    opacity: 1 !important;
    background: #0000ff !important;
    color: #fff;
  }
}

// Bottom modal
.modal-bottom {
  display: flex;
  flex-flow: column nowrap;
  -ms-flex-pack: end;
  justify-content: flex-end;
  height: 100%;
  margin: 0 auto;
  align-content: center;
}

// Colored modal header
.modal-colored-header {
  color: $white;
  border-radius: 0;

  .close {
    color: $white !important;
  }
}

.modal-dialog {
  margin: 1.7em auto;
  width: calc(100% - 40px);
}

.modal-body {
  @media (max-width: 479px) {
    padding: 20px;
  }
}

.modal-backdrop.show {
  opacity: 0.85;
}

button.close {
  background-color: #b6ffe8;
}
