}

// Font Weight

.font-weight-medium {
  font-weight: $font-weight-medium !important;
}

.font-weight-semibold {
  font-weight: $font-weight-semibold !important;
}

.font-weight-regular {
  font-weight: $font-weight-regular !important;
}

.font-weight-bold {
  font-weight: $font-weight-bold !important;
}

// Line Height

.lh-1-3 {
  line-height: 1.33333;
}

.lh-1-195 {
  line-height: 1.195;
}

.lh-1 {
  line-height: 1;
}

// Color
.color-primary {
  color: #212b35;
}

.color-secondary {
  color: #454f5b;
}

.color-black {
  color: #000;
}

.color-danger {
  color: #ed1c24;
}
